}

.img-window-background {
  z-index: -10;
  position: fixed;
  width: 100%;
  height: auto;
  /* background-repeat: repeat-y; */
  overflow: hidden;
}

.img-phone-background {
  z-index: -10;
  position: fixed;
  width: auto;
  height: 100%;
  background: repeat;
  overflow: hidden;
}
